<template>
  <!--  -->
  <el-container style="height: 100vh">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu router :default-active="activePath" unique-opened>
        <!--  -->
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-s-fold"></i>
          </template>
          <!--  -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/category"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <!--  -->
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-menu"></i>
          </template>
          <!--  -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/item"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <!--  -->
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-s-help"></i>
          </template>
          <!-- -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item
              :index="$route.path"
              v-if="$route.path==='/hero/create'||$route.path.indexOf('/hero/edit')!==-1"
            >{{AddOrEdit}}</el-menu-item>
            <el-menu-item index="/hero/list"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <!--  -->
        <el-submenu index="4">
          <template slot="title">
            <i class="el-icon-document"></i>
          </template>
          <!-- -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item
              :index="$route.path"
              v-if="$route.path==='/article/create'||$route.path.indexOf('/article/edit')!==-1"
            >{{AddOrEdit}}</el-menu-item>
            <el-menu-item index="/article/list"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <!--  -->
        <el-submenu index="5">
          <template slot="title">
            <i class="el-icon-s-opportunity"></i>
          </template>
          <!-- -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/ad/list"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <!--  -->
        <el-submenu index="6">
          <template slot="title">
            <i class="el-icon-user"></i>
          </template>
          <!-- -->
          <el-menu-item-group>
            <template slot="title"></template>
            <el-menu-item index="/admin_user/list"></el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-aside>

    <el-container>
      <!--  -->
      <el-header style="text-align: right; font-size: 12px">
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button type="primary" size="small" @click="loginOut"></el-button>
      </el-header>
      <!--  -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'Main',
  data() {
    return {
    }
  },
  computed: {
    activePath() {
      return this.$route.path
    },
    AddOrEdit() {
      if (this.$route.path.indexOf('/hero') !== -1) {
        if (this.$route.path === '/hero/create') {
          return ''
        } else if (this.$route.path.indexOf('/hero/edit') !== -1) {
          return ''
        }
      } else {
        if (this.$route.path === '/article/create') {
          return ''
        } else if (this.$route.path.indexOf('/article/edit') !== -1) {
          return ''
        }
      }

    }
  },
  methods: {
    // 
    loginOut() {
      sessionStorage.clear()
      this.$router.replace('/login')
    }
  }
}
</script>

<style scoped>
</style>